@charset "utf-8";

.kolonsport-site {
  .kolon-wrap {
    .kolon-header {
      .h-wrap {
        height: 15.066vw;
        ul {
          border-bottom: 0;
        }
        .h1-logo a {
          width: 12.5vw;
          height: 9.86vw;
          margin-left: 0;
          background: url(/_ui/img/kolonsport/m/logo.png) no-repeat;
          background-size: contain;
          transform: translate(-50%,-50%);
        }
        .cart {
          width: 8.66vw;
          background: url(/_ui/img/kolonsport/m/icn/icn-cart.png) no-repeat center center;
          background-size: 3.46vw;
        }
      } 
    }
  }
  .kolon-wrap {
    &.main {
      .h-wrap {
        height: 15.066vw;
        ul li {
          .search {
            width: 5vw;
            height: 6vw;
            left: 72%;
            background: url(/_ui/img/kolonsport/m/icn/icn-search.png) no-repeat center;
            background-size: 3.733vw;
          }
        }
      }
    }
    ul li {
      .search {
        width: 5vw;
        height: 6vw;
        left: 56%;
        background: url(/_ui/img/kolonsport/m/icn/icn-search.png) no-repeat center;
        background-size: 3.733vw;
      }
    }
  }
  footer .footer .f-wrap {
    dl dd a {
      background: none;
      text-decoration: none;
      &.business {
        border-bottom: 1px solid;
        padding-bottom: 1px;
        padding-right: 0;
      }
    }
    .copyright a {
      background: url("/_ui/img/m/icn/icn-arrow09.png") no-repeat right center;
      text-decoration: none;
      background-size: 1.333vw auto;
    }
    .gnb {
      padding-bottom: 3.733vw;
      border-bottom: 1px solid #aaa;
      ul {
        display: flex;
        text-align: center;
        li {
          margin-left: 0;
          flex: auto;
        }
      }
    }
    .box-sns {
      padding: 6.266vw 0;
      a, img {
        vertical-align: middle;
      }
      a {
        font-size: 0;
        img {
          width: 4vw;
          margin-right: 8vw;
        }
      }
      .award {
        width: 40.933vw;
      }
    }
  }
}

@media (min-width: 750px) {
  .kolonsport-site {
    .kolon-wrap {
      .kolon-header {
        .h-wrap {
          height: 113px;
          .h1-logo a {
            width: 90px;
            height: 71px;
          }
          .search {
            width: 37px;
            height: 45px;
            background-size: 28px;
          }
          .cart {
            width: 65px;
            background-size: 26px;
          }
        }
      }
      &.main {
        .h-wrap {
          ul li {
            .search {
              width: 37.5px;
              height: 45px;
              background-size: 28px;
            }
          }
        }
      }
    }
    footer .footer .f-wrap {
      .copyright a {
        background: url("/_ui/img/m/icn/icn-arrow09.png") no-repeat right center;
        background-size: 10px auto;
      }
      .gnb {
        padding-bottom: 28px;
      }
      .box-sns {
        padding: 47px 0;
        a, img {
          vertical-align: middle;
        }
        a {
          font-size: 0;
          img {
            width: 30px;
            margin-right: 60px;
          }
        }
        .award {
          width: 307px;
        }
      }
    }
  }
}