@charset "utf-8";

.kolonsport-site {
  .scr-navi {
    height: 15.6vw;
    border-top: 2px solid #e8e8e8;
    .swiper-slide {
      padding: 0 5.333vw;
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }
    a {
      height: auto;
      line-height: 5.333vw;
      border-bottom: .533vw solid #fff;
      &:hover {
        border-bottom: .533vw solid #000;
      }
    }
  }

  .fixed-navi {
    height: 15.6vw;
    border-top: 2px solid #e8e8e8;
    .wrap-navi-list {
      overflow: hidden;
      padding: 0 4.666vw;
      line-height: 15.6vw;
      .navi-list {
        float: left;
        padding: 0 4.266vw;
        font-size: 3.466vw;
        &:first-child {
          padding-left: 0;
        }
        &:last-child {
          padding-right: 0;
        }
      }
      a {
        color: #adadad;
        &.on {
          color: #1f1f1f;
        }
      }
    }
  }

  .square-banner {
    a {
      .txt-box {
        .tit {
          font-size: 6.66vw;
        }
        span {
          font-size: 3.733vw;
        }
      }
    }
  }
  .scr-prod {
    .swiper-wrapper {
      .swiper-slide {
        margin: 0 .933vw;
        .prod-img {
          position: relative;
          .list-no {
            position: absolute;
            display: block;
            width: 5.6vw;
            height: 5.6vw;
            line-height: 5.33vw;
            top: 1.2vw;
            right: 1.466vw;
            background: #dfdfdf;
            font-size: 3.2vw;
            color: #fff;
            text-align: center;
            border-radius: 50%;
          }
          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.03);
          }
        }
        .txt-box {
          padding-top: 2.666vw;
          .tit {
            white-space: normal;
            height: 9.7vw;
          }
          .btm {
            height: auto;
            .price {
              position: static;
              &.sold-out {
                color: #9ba4b0;
              }
            }
          }
        }
      }
    }
  }
  .bnr-more {
    display: block;
    width: 7.733vw;
    height: 7.733vw;
    background: url(/_ui/img/m/icn/icn-more.png) no-repeat;
    background-size: 100%;
    font-size: 0;
    text-indent: -9999px;
    margin: 5.733vw auto 10.533vw;
  }
  .list2-prod > ul li {
    .tit {
      white-space: normal;
      height: 9.7vw;
    }
    .txt-box {
      .price {
        padding-top: 0;
        &.sold-out {
          color: #9ba4b0;
        }
      }
    }
  }
}

@media (min-width: 750px) {
  .kolonsport-site {
    .scr-navi {
      height: 117px;
      .swiper-slide {
        padding: 0 40px;
      }
      a {
        line-height: 40px;
        &:hover {
          border-bottom: 4px solid #000;
        }
      }
    }
    .fixed-navi {
      height: 117px;
      .wrap-navi-list {
        padding: 35px;
        line-height: 50px;
        .navi-list {
          padding: 0 32px;
          font-size: 26px;
        }
      }
    }
    .square-banner {
      a {
        .txt-box {
          .tit {
            font-size: 50px;
          }
          span {
            font-size: 28px;
          }
        }
      }
    }
    .scr-prod {
      .swiper-wrapper {
        .swiper-slide {
          margin: 0 7px;
          .prod-img {
            .list-no {
              width: 42px;
              height: 42px;
              line-height: 40px;
              top: 9px;
              right: 11px;
              font-size: 24px;
            }
          }
          .txt-box {
            padding-top: 20px;
            .tit {
              white-space: normal;
              height: 72px;
            }
            .btm {
              height: auto;
            }
          }
        }
      }
    }
    .bnr-more {
      width: 58px;
      height: 58px;
      margin: 43px auto 79px;
    }
    .list2-prod > ul li {
      .tit {
        white-space: normal;
        height: 72px;
      }
      .txt-box {
        .price {
          padding-top: 0;
        }
      }
    }
  }
}
